import React from 'react';
import { Table, FormText, Row, Col, Button, Input, FormGroup, Form } from 'reactstrap';
const GLOBAL = require('../api')

export default class Settings extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      //payment list
      daftar: [],

      //user list
      user: [],

      //payment name value
      nama_pembayaran: '', peruntukan: '', id: '',

      //user selected 
      idadmin: '', pass1: '', pass2: '', level: '',

      //useradd
      username: '', namalengkap: '', email: '', newLevel: '', password: '',

      //view component
      paymentAdd: false, paymentNameUpdate: false, editUser: false, listUser: false, userAdd: false
    }
  }

  //get payment name
  getPaymentName() {
    fetch(GLOBAL.LINK + '/nama/pembayaran')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ daftar: [] })
        } else {
          this.setState({ daftar: res.records })
        }
      })
  }

  //get user list
  getUserList() {
    fetch(GLOBAL.LINK + '/user')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ user: [] })
        } else {
          this.setState({ user: res.records, listUser: true, userAdd: false })
        }
      })
  }

  //selected
  selectedPaymentName(id, nama, peruntukan) {
    this.setState({
      nama_pembayaran: nama,
      peruntukan: peruntukan,
      id: id,
      paymentAdd: true,
      paymentNameUpdate: true
    })
  }

  selectedUser(id) {
    this.setState({
      idadmin: id,
      editUser: true
    })
  }

  //changePassword
  changePassword() {
    const { idadmin, pass1, pass2 } = this.state
    if (pass1 !== pass2) alert('Password tidak sama')
    else {
      const data = new FormData()
      data.append('id', idadmin)
      data.append('passnew', pass1)
      fetch(GLOBAL.LINK + '/reset/password', {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then(res => {
          if (res.status === 200) {
            console.log(res)
            alert('Password diperbarui')
            this.setState({
              pass1: '',
              pass2: ''
            })
          } else {
            alert('Password gagal diperbarui')
          }
        })
    }
  }


  //add payment name
  paymentNameAdd() {
    const { nama_pembayaran, peruntukan } = this.state
    if (nama_pembayaran === '') alert('Nama pembayaran kosong')
    else if (peruntukan === '') alert('Peruntukan pembayaran kosong')
    else {
      const data = new FormData()
      data.append('nama_pembayaran', nama_pembayaran);
      data.append('peruntukan', peruntukan);
      fetch(GLOBAL.LINK + '/nama/pembayaran/add', {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then((res) => {
          console.log(res)
          if (res.status === 201) {
            this.getPaymentName()
            alert('Berhasil ditambahkan')
          } else {
            this.setState({
              isLoading: false
            })
            alert('Gagal ditambahkan')
          }
        })
    }
  }


  //save data admin
  saveData() {
    const { username, namalengkap, email, level } = this.state
    if (username === '') alert('Username kosong')
    else if (namalengkap === '') alert('Nama lengkap kosong')
    else if (email === '') alert('Email kosong')
    else if (level === '') alert('Level kosong')
    else {
      this.setState({ loading: true })
      const data = new FormData()
      data.append('username', username)
      data.append('namalengkap', namalengkap)
      data.append('email', email)
      data.append('level', level)

      fetch(GLOBAL.LINK + '/user/add', {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then(res => {
          console.log(res)
          if (res.status === 201) {
            this.setState({
              username: '', password: '', email: '', newLevel: '', namalengkap: ''
            })
            alert('Berhasil ditambahkan')
            this.getUserList()
          } else {
            alert('Gagal ditambahkan')
          }
        })
    }
  }

  //update payment name
  paymentNameUpdate() {
    const { nama_pembayaran, peruntukan, id } = this.state
    if (nama_pembayaran === '') alert('Nama pembayaran kosong')
    else if (peruntukan === '') alert('Peruntukan pembayaran kosong')
    else {
      const data = new FormData()
      data.append('nama_pembayaran', nama_pembayaran);
      data.append('peruntukan', peruntukan);
      fetch(GLOBAL.LINK + '/nama/pembayaran/update/' + id, {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            this.getPaymentName()
            alert('Berhasil diperbarui')
          } else {
            this.setState({
              isLoading: false
            })
            alert('Gagal diperbarui')
          }
        })
    }
  }

  //delete payment name
  deleteAlert(id) {
    if (window.confirm('Hapus pembayaran')) { this.deletePayment(id) }
  }
  deletePayment(id) {
    fetch(GLOBAL.LINK + '/nama/pembayaran/delete/' + id, {
      method: 'DELETE'
    })
      .then(respone => respone.json())
      .then(res => {
        if (res.status === 200) {
          alert('Berhasil dihapus')
          this.getPaymentName()
        } else {
          alert('Gagal dihapus')
        }
      })
  }

  //delete userlist
  deleteAlertUser(id) {
    if (window.confirm('Hapus user ?')) { this.deleteUser(id) }
  }
  deleteUser(id) {
    fetch(GLOBAL.LINK + '/user/delete/' + id, {
      method: 'DELETE'
    })
      .then(respone => respone.json())
      .then(res => {
        if (res.status === 200) {
          alert('Berhasil dihapus')
          this.getUserList()
        } else {
          alert('Gagal dihapus')
        }
      })
  }

  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    var parsed = JSON.parse(checkLocalStorage)
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      } else {
        this.setState({
          userLogin: true,
          nama: parsed.nama,
          level: parsed.level
        })
      }
    } else {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.reload()
    }
    if (checkSessionStorage === null) {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.reload()
    }
  }

  componentDidMount() {
    this.getPaymentName()
  }
  componentWillMount() {
    this.timer = setInterval(() => this.checkSession(), 1000)
  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  render() {
    return (
      <Row>
        <Col>
          <Col>
            {
              this.state.paymentAdd ?
                null
                :
                <Button onClick={() => this.setState({ paymentAdd: true })} color="primary">Tambah Nama Pembayaran</Button>
            }
            {
              this.state.paymentAdd ?
                <div>
                  {
                    this.state.paymentNameUpdate ?
                      <Button onClick={() => this.paymentNameUpdate()} color="primary">Perbarui</Button>
                      :
                      <Button onClick={() => this.paymentNameAdd()} color="primary">Simpan</Button>
                  }
                  {" "}<Button onClick={() => this.setState({ paymentAdd: false, paymentNameUpdate: false, id: '', nama_pembayaran: '', peruntukan: '' })} color="secondary">Batal</Button>

                  <hr />
                  <FormGroup>
                    <Input value={this.state.nama_pembayaran} onChange={event => this.setState({ nama_pembayaran: event.target.value })} type="text" placeholder="Nama pembayaran" />
                  </FormGroup>
                  <FormGroup>
                    <Input value={this.state.peruntukan} onChange={event => this.setState({ peruntukan: event.target.value })} type="text" placeholder="Peruntukan" />
                  </FormGroup>
                </div> : null
            }
            <hr />
            <div style={{ height: 500, overflow: 'scroll', fontSize: 13 }} >
              <Table>
                <thead>
                  <tr>
                    <th>Nama Pembayaran</th>
                    <th>Peruntukan</th>
                    <th>Opsi</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    this.state.daftar.map((item, idx) => {
                      return (
                        <tr key={idx} >
                          <th scope="row">{item.nama_pembayaran}</th>
                          <td>
                            {item.peruntukan === "0" ? "Pembayaran Semua kelas" : ""}
                            {item.peruntukan === "1" ? "Pembayaran Kelas X" : ""}
                            {item.peruntukan === "2" ? "Pembayaran Kelas XI" : ""}
                            {item.peruntukan === "3" ? "Pembayaran Kelas XII" : ""}
                            {item.peruntukan === "4" ? "Pembayaran Opsional" : ""}
                          </td>
                          <td>
                            <Button color="info" onClick={() => this.selectedPaymentName(item.id, item.nama_pembayaran, item.peruntukan)} >Edit</Button>{" "}
                            <Button color="danger" onClick={() => this.deleteAlert(item.id)} >Hapus</Button>
                          </td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>
            </div>
          </Col>
          <hr />
          <Col>
            <FormText color="muted">
              <b>* Kategori Peruntukan</b>
              <b>
                <ul>
                  <li>Kode 0 Untuk pembayaran semua kelas</li>
                  <li>Kode 1 Untuk pembayaran kelas X</li>
                  <li>Kode 2 Untuk pembayaran kelas XI</li>
                  <li>Kode 3 Untuk pembayaran kelas XII</li>
                  <li>Kode 4 Untuk pembayaran Optional</li>
                </ul>
              </b>
            </FormText>
          </Col>
        </Col>
        <Col>
          {
            this.state.level === '3' ?
              <Button onClick={() => this.setState({ userAdd: true, listUser: false })} color="primary">Tambah User</Button>
              : null
          }
          {" "}
          {
            this.state.level === '3' ?
              <Button onClick={() => this.getUserList()} color="secondary">Daftar User</Button>
              : null
          }
          <hr />
          {
            this.state.editUser ?
              <div>
                <Button onClick={() => this.changePassword()} color="primary">Simpan</Button>
                {" "}<Button onClick={() => this.setState({ editUser: false, pass1: '', pass2: '' })} color="secondary">Batal</Button>

                <hr />
                <FormGroup>
                  <Input value={this.state.pass1} onChange={event => this.setState({ pass1: event.target.value })} type="password" placeholder="Password baru" />
                </FormGroup>
                <FormGroup>
                  <Input value={this.state.pass2} onChange={event => this.setState({ pass2: event.target.value })} type="password" placeholder="Konfirmasi Password" />
                </FormGroup>
                <hr />
              </div>
              : null
          }
          {
            this.state.listUser ?
              <div style={{ height: 500, overflow: 'scroll', fontSize: 13 }} >
                <Table>
                  <thead>
                    <tr>
                      <th>Username</th>
                      <th>Nama</th>
                      <th>email</th>
                      <th>Level</th>
                      <th>Opsi</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.user.map((item, idx) => {
                        return (
                          <tr key={idx} >
                            <th scope="row">{item.username}</th>
                            <th scope="row">{item.nama}</th>
                            <td> {item.email} </td>
                            <td>
                              {item.level === '3' ? "Super Administrator" : "Administrator"}
                            </td>
                            <td>
                              {
                                item.id === '1' ?
                                  null :
                                  <div>
                                    <Button color="info" onClick={() => this.selectedUser(item.id)} >Password</Button>{" "}
                                    <Button color="danger" onClick={() => this.deleteAlertUser(item.id)} >Hapus</Button>
                                  </div>
                              }
                            </td>
                          </tr>
                        )
                      })
                    }
                  </tbody>
                </Table>
              </div> : null
          }
          {
            this.state.userAdd ?
              <Form>
                <FormGroup>
                  <Input type="text" id="input" onChange={event => this.setState({ username: event.target.value })} value={this.state.username} placeholder="username" />
                </FormGroup>
                <FormGroup>
                  <Input type="text" id="input2" onChange={event => this.setState({ namalengkap: event.target.value })} value={this.state.namalengkap} placeholder="Nama Lengkap" />
                </FormGroup>
                <FormGroup>
                  <Input type="text" id="input3" onChange={event => this.setState({ email: event.target.value })} value={this.state.email} placeholder="Email" />
                </FormGroup>
                <FormGroup>
                  <Input type="select" onChange={event => this.setState({ newLevel: event.target.value })} >
                    <option value="2">Administrator</option>
                    <option value="3">Super Administrator</option>
                  </Input>
                </FormGroup>
                <FormGroup>
                  <Input type="password" id="input5" onChange={event => this.setState({ password: event.target.value })} value={this.state.password} placeholder="Password" />
                </FormGroup>
                <Button onClick={() => this.saveData()} color="primary" >Submit</Button> {" "}
                <Button onClick={() => this.setState({ userAdd: false })} color="secondary" >Batal</Button>
              </Form> : null
          }
        </Col>
      </Row>
    );
  }
}


