import React, { Component } from 'react';
import { Container, Row, Col, Nav, NavItem, Button, Jumbotron } from 'reactstrap';
import Siswa from './pages/Siswa'
import Settings from './pages/Settings'
import Account from './pages/Account'
import Login from './pages/Login'
import Information from './pages/Information'
import Report from './pages/Laporan'


export default class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      link: 'Login',
      nama: '',
      userLogin: false
    }
  }
  clickState(link) {
    this.setState({
      link: link
    })
  }
  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    var parsed = JSON.parse(checkLocalStorage)
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      } else {
        this.setState({
          link: null,
          userLogin: true,
          nama: parsed.nama
        })
      }
    }
  }

  componentWillMount() {
    this.checkSession()
  }
  logoutAlert() {
    if (window.confirm('Logout ?')) { this.logOut() }
  }
  logOut() {
    localStorage.removeItem('DataLogin')
    sessionStorage.removeItem('DataLogin')
    this.setState({
      link: 'Login'
    })
  }
  render() {
    if (this.state.link === 'Login') {
      return (
        <div style={{ padding: 20 }} >
          <Login />
        </div>
      )
    }
    return (
      <div style={{ padding: 20 }} >
        <Container>
          <Row>
            <Col>
              <Nav>
                <NavItem>
                  <Button color="link" onClick={() => this.clickState(null)}>Home</Button>
                </NavItem>
                <NavItem>
                  <Button color="link" onClick={() => this.clickState('Siswa')}>Siswa</Button>
                </NavItem>
                <NavItem>
                  <Button color="link" onClick={() => this.clickState('Settings')}>Pengaturan</Button>
                </NavItem>
                <NavItem>
                  <Button color="link" onClick={() => this.clickState('Akun')}>Akun</Button>
                </NavItem>
                <NavItem>
                  <Button color="link" onClick={() => this.clickState('Laporan')}>Laporan</Button>
                </NavItem>
              </Nav>
              <hr />
            </Col>
          </Row>
          <Row>
            <Col>
              <div>
                {this.state.link === null ?
                  <div>
                    <Jumbotron>
                      <h1 className="display-4">Selamat Datang Admin {this.state.nama} </h1>
                      <p className="lead">Gunakan Aplikasi ini dengan bijak</p>
                      <Button onClick={() => this.logoutAlert()} color="danger"> Logout </Button>
                    </Jumbotron>
                    <Information />
                  </div>
                  : null}
                {this.state.link === 'Siswa' ? <Siswa /> : null}
                {this.state.link === 'Settings' ? <Settings /> : null}
                {this.state.link === 'Akun' ? <Account /> : null}
                {this.state.link === 'Laporan' ? <Report /> : null}
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}




