import { CSVLink } from 'react-csv'
import ReactTable from "react-table"
import ReactToPrint from "react-to-print"
import React, { Component } from 'react';
import { Row, Input, Col, Button, Table, Container } from 'reactstrap'
const GLOBAL = require('../api')
export default class Report extends Component {
  constructor(props) {
    super(props)
    this.state = {
      list: [],
      total: '',
      judul: '',
      costumDate: false,
      start: '',
      end: '',
      reSult: false,
      print: false
    }
  }

  getDataHarian() {
    this.setState({
      costumDate: false
    })
    fetch(GLOBAL.LINK + '/laporan/harian')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Laporan Harian', reSult: true })
        }
      })
  }
  getDataBulanan() {
    this.setState({
      costumDate: false
    })
    fetch(GLOBAL.LINK + '/laporan/bulanan')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Laporan Bulanan', reSult: true })
        }
      })
  }
  getDataTahunan() {
    this.setState({
      costumDate: false
    })
    fetch(GLOBAL.LINK + '/laporan/tahunan')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Laporan Tahunan', reSult: true })
        }
      })
  }
  getDataCustom() {
    this.setState({
      costumDate: true
    })
  }
  getResultCustom() {
    fetch(GLOBAL.LINK + '/laporan/custom/' + this.state.start + '/' + this.state.end)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Laporan tanggal ' + this.state.start + ' sampai ' + this.state.end, reSult: true })
        }
      })
  }


  printTb() {
    this.setState({
      print: true
    })
  }

  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      }
    } else {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.reload()
    }
    if (checkSessionStorage === null) {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.reload()
    }
  }

  componentWillMount() {
    this.timer = setInterval(() => this.checkSession(), 1000)
  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  render() {
    if (this.state.print) {
      return (
        <div style={{ margin: 20 }} >
          <ReactToPrint
            trigger={() => <a href="">Print</a>}
            content={() => this.componentRef}
            copyStyles={true}
            onBeforePrint={() => alert('Print data')}
          />
          <ComponentToPrint data={this.state.list} judul={this.state.judul} total={this.state.total} ref={el => (this.componentRef = el)} />
        </div>
      )
    }
    // data for csv
    const data = this.state.list

    //title header csv
    const headers = [
      { label: 'Nama', key: 'nama' },
      { label: 'Kelas', key: 'kelas' },
      { label: 'Jurusan', key: 'jurusan' },
      { label: 'Nama Pembayaran', key: 'nama_pembayaran' },
      { label: 'Nilai', key: 'nilai' },
      { label: 'Tgl Pembayaran', key: 'tgl_pembayaran' },
      { label: 'Petugas', key: 'admin' },
    ];

    //title table list
    const colums = [
      {
        Header: "Nama",
        accessor: "nama",
        width: 300
      },
      {
        Header: "Kelas",
        id: "kelas",
        accessor: d => d.kelas + " - " + d.jurusan,
        style: {
          textAlign: 'center'
        }
      },
      {
        Header: "Nama Pembayaran",
        id: "payname",
        accessor: d => d.nama_pembayaran
      },
      {
        Header: "Nilai",
        id: "nilai",
        accessor: d => d.nilai
      },
      {
        Header: "Tgl",
        id: "tgl",
        accessor: d => d.tgl_pembayaran,
        style: {
          textAlign: 'center'
        }
      },
      {
        Header: "Petugas",
        id: "petugas",
        accessor: d => d.admin
      },
    ]
    return (
      <Row>
        <Col>
          <Button color="primary" onClick={() => this.getDataHarian()} > Harian </Button>{" "}
          <Button color="primary" onClick={() => this.getDataBulanan()} > Bulanan </Button>{" "}
          <Button color="primary" onClick={() => this.getDataTahunan()}> Tahunan </Button>{" "}
          {
            this.state.costumDate ?
              <Button color="primary" onClick={() => this.getResultCustom()}> Proses </Button> :
              <Button color="primary" onClick={() => this.getDataCustom()}> Custom </Button>
          }
          <hr />
          {
            this.state.costumDate ?
              <Row>
                <Col>
                  <Input onChange={event => this.setState({ start: event.target.value })} type="date" />
                </Col>
                <Col>
                  <Input onChange={event => this.setState({ end: event.target.value })} type="date" />
                </Col>
              </Row>
              : null
          }
          {
            this.state.reSult ?
              <div>
                <hr />
                <b>{this.state.judul}</b>
                <br />
                <br />
                <ReactTable
                  showPaginationTop={true}
                  showPaginationBottom={false}
                  data={this.state.list}
                  columns={colums}
                  defaultPageSize={10}
                  className="-striped -highlight"
                />
                <hr />
                <b> Dengan total : {this.state.total}  </b>
                <hr />
                <CSVLink data={data} filename={this.state.judul + ".csv"} headers={headers} >Download Report</CSVLink>
                {" "}
                <Button color="primary" onClick={() => this.printTb()} > Print </Button>
              </div> : null
          }
        </Col>
      </Row>
    );
  }
}

class ComponentToPrint extends React.Component {
  render() {
    return (
      <div style={{ margin: 20 }} >
        <Container>
          <hr />
          <b style={{ textAlign: 'center' }} > {this.props.judul}  </b>
          <br />
          <br />
          <Table >
            <thead>
              <tr>
                <th>Nama</th>
                <th>Kelas</th>
                <th>Nama Pembayaran</th>
                <th>Nilai</th>
                <th>Tgl</th>
                <th>Petugas</th>
              </tr>
            </thead>
            <tbody style={{ fontSize: 12 }}>
              {
                this.props.data.map((item, idx) => {
                  return (
                    <tr key={idx} >
                      <th scope="row"> {item.nama} </th>
                      <td>{item.kelas} - {item.jurusan}</td>
                      <td>{item.nama_pembayaran}</td>
                      <td>{item.nilai}</td>
                      <td>{item.tgl_pembayaran}</td>
                      <td>{item.admin}</td>
                    </tr>
                  )
                })
              }
            </tbody>
          </Table>
          <b> Dengan total : {this.props.total}  </b>
        </Container>
      </div>
    );
  }
}


