import React from 'react';
import ReactTable from "react-table"
import { Button, FormGroup, Input, ModalBody, ModalHeader, ModalFooter, Modal } from 'reactstrap'
const GLOBAL = require('../api')
export default class Information extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      //view component
      infoAdd: false,

      //information
      info: [], type: null,

      //modal
      modal: false, deskripsi: '', judul: '', id: '',

      //input value
      title: '', desc: ''
    }
  }

  //fetch data
  fetchData() {
    fetch(GLOBAL.LINK + '/informasi/admin')
      .then(response => response.json())
      .then(res => {
        this.setState({
          info: res.records
        })
      })
  }
  componentDidMount() {
    this.fetchData()
  }

  //selected info
  selectedInfo(id, title, desc) {
    this.setState({
      id: id,
      title: title,
      desc: desc,
      infoAdd: true, type: 'update'
    })
  }

  //fcm send
  FirebaseNotification(title, msg) {
    fetch('https://fcm.googleapis.com/fcm/send', {
      method: 'POST',
      headers: {
        "authorization": "key=AIzaSyB0UGwE1GX2QUKzWoZpOang9FYdLOz1II8",
        "content-type": "application/json"
      },
      body: JSON.stringify({
        "to": "/topics/allnew",
        "data": {
          "custom_notification": {
            "title": title,
            "body": msg,
            "color": "#c62828",
            "priority": "high",
            "icon": "ic_notif",
            "sound": "default",
            "show_in_foreground": true,
            "wake_screen": true,
          }
        }
      })
    })
  }

  sendNotificationMessage() {
    const { title, desc } = this.state
    if (title === null) alert('Judul kosong')
    else if (desc === null) alert('Pesan kosong')
    else {
      var data = new FormData();
      data.append('judul', title);
      data.append('deskripsi', desc);
      fetch(GLOBAL.LINK + '/informasi/add', {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then((res) => {
          if (res.status === 201) {
            alert('Informasi ditambahkan')
            this.FirebaseNotification(title, desc)
            this.fetchData()
            this.setState({
              infoAdd: false
            })
          } else {
            alert('Informasi gagal ditambahkan')
          }
        })
    }
  }

  //update process
  updateInformation() {
    const { title, desc } = this.state
    if (title === null) alert('Judul kosong')
    else if (desc === null) alert('Pesan kosong')
    else {
      var data = new FormData();
      data.append('judul', title);
      data.append('deskripsi', desc);
      fetch(GLOBAL.LINK + '/informasi/update/' + this.state.id, {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then((res) => {
          if (res.status === 200) {
            alert('Informasi diperbarui')
            this.fetchData()
            this.setState({
              infoAdd: false
            })
          } else {
            alert('Informasi gagal diperbarui')
          }
        })
    }
  }

  viewModal(judul, deskripsi) {
    this.setState({
      modal: true,
      deskripsi: deskripsi,
      judul: judul
    })
  }

  //delete information
  deleteAlert(id) {
    if (window.confirm('Hapus Informasi ?')) { this.deletePayment(id) }
  }
  deletePayment(id) {
    fetch(GLOBAL.LINK + '/informasi/delete/' + id, {
      method: 'DELETE'
    })
      .then(respone => respone.json())
      .then(res => {
        if (res.status === 200) {
          alert('Berhasil dihapus')
          this.fetchData()
        } else {
          alert('Gagal dihapus')
        }
      })
  }
  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      }
    } else {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      }
    if (checkSessionStorage === null) {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.reload()
    }
  }

  componentWillMount() {
    this.timer = setInterval(() => this.checkSession(), 1000)
  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  render() {
    return (
      <div>
        <Button color="primary" onClick={() => this.setState({ infoAdd: true })} > Tambah Informasi </Button>
        <hr />
        {
          this.state.infoAdd ?
            <div>
              <FormGroup>
                <Input type="text" onChange={event => this.setState({ title: event.target.value })} value={this.state.title} placeholder="Judul pesan" />
              </FormGroup>
              <FormGroup>
                <Input type="textarea" onChange={event => this.setState({ desc: event.target.value })} name="text" id="exampleText" value={this.state.desc} placeholder="Isi pesan" />
              </FormGroup>
              {
                this.state.type === 'update' ?
                  <Button color="primary" onClick={() => this.updateInformation()} >Update</Button>
                  :
                  <Button color="primary" onClick={() => this.sendNotificationMessage()} >Kirim</Button>
              }
              {" "}<Button color="secondary" onClick={() => this.setState({ infoAdd: false })} >Batal</Button>
              <hr />
            </div> : null
        }
        <ReactTable
          showPaginationTop={true}
          showPaginationBottom={false}
          data={this.state.info}
          columns={
            [
              { Header: "Judul", accessor: "judul" },
              { Header: "Isi", id: "isi",  accessor: item => item.deskripsi, width: 400 },
              { Header: "Tanggal Infromasi", accessor: "tgl", style: { textAlign: 'center' } },
              { Header: "Option", id: "opsi" , accessor: item => 
              <span>
                <Button color="link" onClick={() => this.viewModal(`${item.judul}`, `${item.deskripsi}`)} > more </Button> 
                        <Button color="info" onClick={() => this.selectedInfo(item.id, item.judul, item.deskripsi)} >Edit</Button>{" "}
                <Button color="danger" onClick={() => this.deleteAlert(item.id)} >Hapus</Button>  </span> },
            ]
              }
          defaultPageSize={5}
          className="-striped -highlight"
        />
        <div>
          <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
            <ModalHeader toggle={this.toggle}>{this.state.judul}</ModalHeader>
            <ModalBody>
              {this.state.deskripsi}
            </ModalBody>
            <ModalFooter>
              <Button color="secondary" onClick={() => this.setState({ modal: false })}>Ok</Button>
            </ModalFooter>
          </Modal>
        </div>
      </div>
    );
  }
}


