import React from 'react';
import { Button, Form, FormGroup, Input } from 'reactstrap';
const GLOBAL = require('../api')

export default class Account extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      //component view
      changePass: false, editProfile: false,

      //value profile edit
      username: '', email: '', namalengkap: '', id: '',

      //password value
      pass1: '', pass2: ''
    }
  }

  //backend process
  editProfile() {
    fetch(GLOBAL.LINK + '/user/single/' + this.state.id)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          alert('Data null')
        } else {
          this.setState({
            id: res.record.id,
            username: res.record.username,
            email: res.record.email,
            namalengkap: res.record.nama,
          })
        }
      })
  }
  saveEditProfile() {
    const { username, email, namalengkap } = this.state
    if (username === '') alert('Nama kosong')
    else if (email === '') alert('Email kosong')
    else if (namalengkap === '') alert('Nama Lengkap kosong')
    else {
      const data = new FormData()
      data.append('username', username)
      data.append('namalengkap', namalengkap)
      data.append('email', email)

      fetch(GLOBAL.LINK + '/user/update/' + this.state.id, {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then(res => {
          console.log(res)
          if (res.status === 200) {
            alert('Berhasil diperbarui')
            this.setState({
              editProfile: false
            })
          } else {
            alert('Gagal diperbarui')
          }
        })
    }
  }

  //profile edit change
  clickProfileEdit() {
    this.setState({
      editProfile: true,
      changePass: false
    })
    this.editProfile()
  }

  //change password
  changePassword() {
    const { id, pass1, pass2 } = this.state
    if (pass1 !== pass2) alert('Password tidak sama')
    else {
      const data = new FormData()
      data.append('id', id)
      data.append('passnew', pass1)
      fetch(GLOBAL.LINK + '/reset/password', {
        method: 'POST',
        header: {
          'Accept': 'application/json',
          'Content-type': 'application/json'
        },
        body: data
      }).then((respone) => respone.json())
        .then(res => {
          if (res.status === 200) {
            alert('Password diperbarui')
            this.setState({
              changePass: false,
              pass1: '',
              pass2: ''
            })
          } else {
            alert('Password gagal diperbarui')
          }
        })
    }
  }

  //check session
  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    var parsed = JSON.parse(checkLocalStorage)
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      }
      this.setState({
        id: parsed.id
      })
    } else {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.reload()
      }
    if (checkSessionStorage === null) {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.reload()
    }
  }
  componentWillMount() {
    this.timer = setInterval(() => this.checkSession(), 1000)
  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  render() {
    return (
      <div>
        <Button onClick={() => this.clickProfileEdit()} color="info" >Perbarui Profil</Button>{" "}
        <Button onClick={() => this.setState({ changePass: true, editProfile: false })} color="warning" >Ganti Password</Button>
        <hr />
        {
          this.state.changePass ?
            <Form>
              <FormGroup>
                <Input onChange={event => this.setState({ pass1: event.target.value })} type="password" placeholder="Password baru" />
              </FormGroup>
              <FormGroup>
                <Input onChange={event => this.setState({ pass2: event.target.value })} type="password" placeholder="Konfirmasi password" />
              </FormGroup>
              <Button onClick={() => this.changePassword()} color="primary" >Submit</Button> {" "}
              <Button onClick={() => this.setState({ changePass: false })} color="secondary" >Batal</Button>
            </Form> : null
        }
        {
          this.state.editProfile ?
            <Form>
              <FormGroup>
                <Input type="text" id="input" onChange={event => this.setState({ username: event.target.value })} value={this.state.username} disabled="true" placeholder="username" />
              </FormGroup>
              <FormGroup>
                <Input type="text" id="input2" onChange={event => this.setState({ namalengkap: event.target.value })} value={this.state.namalengkap} placeholder="Nama Lengkap" />
              </FormGroup>
              <FormGroup>
                <Input type="text" id="input3" onChange={event => this.setState({ email: event.target.value })} value={this.state.email} placeholder="Email" />
              </FormGroup>
              <Button onClick={() => this.saveEditProfile()} color="primary" >Submit</Button> {" "}
              <Button onClick={() => this.setState({ editProfile: false })} color="secondary" >Batal</Button>
            </Form> : null
        }
      </div>
    );
  }
}


